#submit {
	margin: 10px;
}

.contact {
		padding-left: 12%;
	padding-right: 12%;
	padding-top: 5%;
	padding-bottom: 5%;

}

label {
	text-align: center;
}

quote {
	display: center;
	color: #000000;
}

@media only screen and (max-width: 700px) {
.form-control {
	font-size: 0.5rem;
	height: calc(1.25rem + 2px);

	}
}
.App {
  padding: 0px;
  margin: 0px;
  background-color: #3c3c3c;
  min-height: 1024px;
  background-size:contain;
  background-position:center;
  width: 100%;
  text-align: left;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  font-size: 1vw;
  font: #FFFFFF;
  -webkit-font-smoothing: auto;
  font-smooth: auto;
  font-family: 'Quantico', sans-serif;
}



#navigation {
    background-size:contain;
    background-position:center;
    background-color: #000000;
    background-repeat: no-repeat;
    clear: both;
}

#slider {
    transition: width 2s ease;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    vertical-align: middle;
    width: 0px;
}

#sliderOut {
    transition: width 2s ease;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    vertical-align: middle;
    width: 310px;
}

.links {
    text-align: center;
    color: #F1F2F3;
}

a {
    padding: 3px;
    margin: 3px;
    color: #F1F2F3;
    cursor: pointer;
    font-size: 1vw;
}

a:hover {
    text-decoration: none;
    color: #FFFFFF;
}

#myStory {
    border-left-style: solid;
    padding-left: 10px;
}

#contact {
    border-right-style: solid;
    padding-right: 10px;
}

.front {
    visibility: visible;
    opacity: 1;
    transition:visibility 2s linear,opacity 2s linear;
}

.back {
   visibility: hidden;
   opacity: 0;
}

h1 {
  text-align: center;
  position: center;
  font-size: 3vw;
}


#scroll-button {
  font-size: 1vw;
}

element.style {
    height: 150px;
}


@media only screen and (max-width: 700px) {
  h1 {
      text-align: center;
  position: center;
  font-size: 5vw;
  }

  #scroll-button {
    font-size: 2vw;
  }

  h6 {
    font-size: 2vw;
  }

  a {
    padding: 3px;
    margin: 3px;
    color: #F1F2F3;
    cursor: pointer;
    font-size: 3.5vw;
  }

}




